@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap');

*{
    outline: none !important;
}

ul,
ol,
p,
div,
label,
h1,h2,h3{
    list-style: none;
    padding: 0;
    margin: 0;
}

button, button span{
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 0;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}



img{
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

a,a:hover{
    text-decoration: none;
}

body,html{
    font-family: 'Monsterrat', sans-serif;
    min-height: 100vh;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(15, 27, 23, .41);
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../images/main-bg.jpg);
}

.loading-page{
    background-image: url(../images/loading-bg.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-page__caption{
    font-weight: 900;
    font-size: 25px;
    line-height: 33px;
    color: #FFFFFF;
    text-align: center;
}

.container__content{
    background-color: #fff;
    border-radius: 32px;
    min-height: calc(100vh - 60px);
    width: calc(100vw - 24px);
    margin: 40px auto 0;
    position: relative;
    display: flex;
    flex-direction: column;
}

.start-page__pic{
    /* position: absolute; */
    display: flex;
    margin-left: -12px;
    margin-right: auto;
    width: 100vw;
    height: auto;
    filter: drop-shadow(0px 22px 41px rgba(105, 163, 242, 0.44));
}

.start-page__pic-wrapper{
    position: relative;
    display: flex;
    /* height: 323px; */
    width: 100%;
    margin-bottom: 20px;
    margin-top: 36px;
}

.start-page__caption{
    font-weight: 900;
    font-size: 20px;
    line-height: 27px;
    text-transform: uppercase;
    color: #0077FF;
    margin-bottom: 25px;
    text-align: center;
}

.start-page__description{
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #0077FF;
    max-width: 290px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
}

.button{
    height: 75px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #008CFF;
    border-radius: 36px;

    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: #FFFFFF;
}

.start-page__button {
    margin-top: 20px;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 50px);
    flex-shrink: 0;
}

.processing-page__rails{
    position: fixed;
    /* bottom: -50px; */
    transform: rotate(-15deg);
    bottom: 140px;
    left: -100px;
}

.processing-page__rail1{
    display: flex;
    animation: processing-rail-to-right 5s linear;
    position: relative;
    margin-bottom: 10px;
}

.processing-page__rail2{
    display: flex;
    animation: processing-rail-to-left 5s linear;
    position: relative;
}

@keyframes processing-rail-to-right {
    from{
        right: 500px;
    }

    to{
        right: 0;
    }
}

@keyframes processing-rail-to-left {
    from{
        right: 0;
    }

    to{
        right: 500px;
    }
}

.processing-page{
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.processing-page__value{
    font-weight: 900;
    font-size: 60px;
    line-height: 80px;
    text-transform: uppercase;
    color: #FFFFFF;
}

.processing-page__caption{
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    margin-bottom: 30vh;
}

.cards-slider{
    padding-top: 75px;
    overflow: hidden;
    padding-bottom: 20px;
    box-sizing: border-box;
    width: 100%;
    /* margin-left: auto;
    margin-right: auto; */
}

.cards-slider .swiper-button-prev{
    width: 29px;
    height: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../images/arrow-icon.svg);
    transform: rotate(180deg);
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    right: calc(100% + 20px);
    z-index: 2;
    bottom: 0;
    transition: .3s;
}

.cards-slider .swiper-button-next{
    width: 29px;
    height: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../images/arrow-icon.svg);
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: calc(100% + 20px);
    z-index: 2;
    bottom: 0;
    transition: .3s;
}

.cards-slider .swiper-button-prev.swiper-button-disabled,
.cards-slider .swiper-button-disabled.swiper-button-next{
    opacity: .4;
}

.cards-slider > .swiper{
    overflow: visible;
    width: 250px;
}

.cards-slide__image-block{
    position: relative;
}

.cards-slide__image-block p{
    background: #FFFFFF;
    box-shadow: 0px 22px 29px rgba(105, 163, 242, 0.18);
    border-radius: 5px;
    width: 210px;
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -13px;
    margin: auto;

    font-weight: 900;
    font-size: 15px;
    line-height: 20px;
    color: #008CFF;
    text-align: center;
}

.cards-slide__text{
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #008CFF;
    max-width: 245px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 25px;
}

.results-page__buttons{
    width: calc(100% - 50px);
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: 30px;
}

.results-page__reload-button{
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: #0077FF;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
}

.cards-slider .swiper-slide .cards-slide__image-block p{
    opacity: 0;
    transition: .3s;
}

.cards-slider .swiper-slide .cards-slide__text{
    opacity: 0;
    transition: .3s;
}

.cards-slider .swiper-slide .cards-slide{
    transform: scale(0.8);
    transition: .3s;
}

.cards-slider .swiper-slide-active .cards-slide__image-block p{
    opacity: 1;
}

.cards-slider .swiper-slide-active .cards-slide__text{
    opacity: 1;
}

.cards-slider .swiper-slide-active .cards-slide{
    transform: scale(1);
}

.share-modal{
    width: 290px;
    min-height: 350px;
    border-radius: 27px;
    box-sizing: border-box;
    background: rgba(235, 237, 240, 0.68);
    border-radius: 27px;
    padding-bottom: 12px;
    position: relative;
}

.share-modal__close{
    width: 47px;
    height: 47px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: calc(100% + 13px);
    left: 0;
    right: 0;
    margin: auto;
}

.share-modal__content{
    border-radius: 27px;
    background: #EBEDF0;
    padding: 100px 20px 20px;
}

.share-modal__pics{
    position: relative;
    height: 0;
    width: 245px;
    margin-left: auto;
    margin-right: auto;
}

.share-modal__pics img:first-child{
    position: absolute;
    bottom: 10px;
    left: 0;
    transform: rotate(-18.03deg);
}

.share-modal__pics img:nth-child(2){
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 2;
}

.share-modal__pics img:last-child{
    position: absolute;
    bottom: 10px;
    right: 0;
    transform: rotate(18.03deg);
}

.share-modal__caption{
    font-weight: 900;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
    color: #0077FF;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 10px;
}

.share-modal__buttons button:first-child{
    margin-bottom: 13px;
}